<template>
    <div>
        <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图区域 -->
    <el-card>
    <el-row :gutter="12">
     <el-col :span="8">
         <el-input placeholder="请输入" v-model="queryInfo.query" clearable @clear='getGoodsList'>
             <el-button slot="append" icon='el-icon-search' @click="getGoodsList()" ></el-button>
         </el-input>
     </el-col>
     <el-col :span='4'>
         <el-button type='primary' @click="goAddpage">添加商品</el-button>
     </el-col>
    </el-row>

    <!-- table表格区域 -->
    <el-table :data='goodsList' border stripe>
        <el-table-column type='index'></el-table-column>
        <el-table-column label="商品名称" prop='goods_name'></el-table-column>
        <el-table-column label="商品价格" prop='goods_price' width='95px'></el-table-column>
        <el-table-column label="商品重量" prop='goods_weight' width='85px'></el-table-column>
        <el-table-column label="创建时间" prop='add_time' width='170px'>
            <template slot-scope="scope">
                {{scope.row.add_time | dateFormat}}
            </template>
        </el-table-column>
        <el-table-column label="操作" width='135px'>
            <template slot-scope="scope">
                <el-button type="primary" icon='el-icon-edit' size="mini">编辑</el-button>
                <el-button type="danger" icon='el-icon-delete' size="mini" @click="removeById(scope.row.goods_id)">删除</el-button>
            </template>
        </el-table-column>

    </el-table>

      <!-- 分页区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>

    </el-card>
    </div>
</template>
<script>
export default {
  data () {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      goodsList: [],
      total: 0
    }
  },
  created () {
    this.getGoodsList()
  },
  methods: {
    // 根据分页获取对应的商品列表
    async getGoodsList () {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品列表失败')
      }
      this.$message.success('获取商品列表成功！')
      this.goodsList = res.data.goods
      this.total = res.data.total
      // console.log(res)
    },
    // 监听 pagesize 该变的事件
    handleSizeChange (newsize) {
      // console.log(newsize)
      this.queryInfo.pagesize = newsize
      this.getGoodsList()
    },

    // 监听 页面值 的改变
    handleCurrentChange (newpage) {
      // console.log(newpage)
      this.queryInfo.pagenum = newpage
      this.getGoodsList()
    },
    // 根据ID 删除 属性
    async removeById (id) {
      const confirmResult = await this.$confirm('此操作将永久删除该账号, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).catch(err => err)

      // 如果用户确定删除，返回值为字符串 confirm
      // 如果用户取消了删除，返回值为字符串 cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }

      const { data: res } = await this.$http.delete(`goods/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('删除用户失败！')
      }

      this.$message.success('删除用户成功')
      this.getGoodsList()
    },
    goAddpage () {
      this.$router.push('/goods/add')
    }
  }
}
</script>
<style lang="less" scoped>
</style>
